<template>
  <div class="flex md:flex-row flex-col md:mx-20 mx-4">
    <div class="m-3 md:w-1/2">
      <LogoComponent />
    </div>
    <div class="m-3 bg-gray-200 min-w-[35%]">
      <MainServeyComponent
        v-show="ServeyComponent"
        @activeLoading="LoadingStepActive"
      />
      <MainLoadingComponent
        v-if="LoadingComponent"
        @activePrize="PrizeStepActive"
      />
      <MainPrizeComponent v-if="PrizeComponent" />
    </div>
  </div>
  <FirstModalComponent />
  <SecondModalComponent />
  <ThirdModalComponent />
  <FourthModalComponent />
</template>

<script>
import LogoComponent from "./LogoComponent.vue";
import MainServeyComponent from "./servey/MainServeyComponent.vue";
import MainLoadingComponent from "./loading/MainLoadingComponent.vue";
import MainPrizeComponent from "./MainPrizeComponent.vue";
import FirstModalComponent from "./modals/FirstModalComponent.vue";
import SecondModalComponent from "./modals/SecondModalComponent.vue";
import ThirdModalComponent from "./modals/ThirdModalComponent.vue";
import FourthModalComponent from "./modals/FourthModalComponent.vue";
import { useStore } from "vuex";
export default {
  name: "MainComponent",
  components: {
    LogoComponent,
    MainPrizeComponent,
    ThirdModalComponent,
    FourthModalComponent,
    MainServeyComponent,
    MainLoadingComponent,
    FirstModalComponent,
    SecondModalComponent,
  },
  data() {
    return {
      ServeyComponent: true,
      LoadingComponent: false,
      PrizeComponent: false,
    };
  },
  computed: {
    new() {
      let store = useStore();
      return store.state.firstModalShow;
    },
  },
  methods: {
    LoadingStepActive() {
      this.ServeyComponent = false;
      this.LoadingComponent = true;
    },
    PrizeStepActive() {
      this.LoadingComponent = false;
      this.PrizeComponent = true;
    },
    start() {
      this.$confetti.start();
    },
  },
  mounted() {
    this.start();
    let store = useStore();
    store.commit("firstModalOn");
  },
};
</script>
