<script>
import { useStore } from "vuex";
export default {
  name: "MainPrizeComponent",
  data() {
    return {
      active: false,
    };
  },
  methods: {
    clicked(e) {
      //   var paret = document.querySelector("#prize" + e);
      var head = document.querySelector("#prize" + e + ">.box-picker__lid");
      var backImg = document.querySelector("#prize" + e + ">.box-picker__back");
      var vm = this;
      if (vm.active) {
        head.classList.toggle("rotateOutUpRight");
        backImg.style.display = "block";
        backImg.classList.toggle("tada");
        setTimeout(function () {
          vm.$confetti.start();
          var store = useStore();
          store.commit("fourthModalOn");
        }, 1000);
      }
      if (!vm.active) {
        head.classList.toggle("rotateOutUpRight");
        vm.active = true;
        setTimeout(function () {
          var store = useStore();
          store.commit("thirdModalOn");
        }, 1000);
      }
    },
  },
};
</script>

