<template>
  <div class="flex flex-col gap-2 justify-center items-center mt-10">
    <p class="font-bold text-xl">...الرجاء الانتظر قليلا</p>
    <div class="flex justify-center items-center gap-1">
      <p
        class="text-lg"
        :style="[
          this.firstLineDone == true
            ? { 'font-weight': 'bold' }
            : { 'font-weight': '' },
        ]"
        v-text="firstLine"
      ></p>
      <img v-if="!firstLineDone" src="../../assets/spinner.gif" alt="" />
      <img v-else src="../../assets/checked.png" width="15" alt="" />
    </div>
    <div class="flex justify-center items-center gap-1">
      <p
        class="text-lg"
        :style="[
          this.secondLineDone == true
            ? { 'font-weight': 'bold' }
            : { 'font-weight': '' },
        ]"
        v-text="secondtLine"
      ></p>
      <img v-if="!secondLineDone" src="../../assets/spinner.gif" alt="" />
      <img v-else src="../../assets/checked.png" width="15" alt="" />
    </div>
    <div class="flex justify-center items-center gap-1">
      <p
        class="text-lg"
        :style="[
          this.thirdLineDone == true
            ? { 'font-weight': 'bold' }
            : { 'font-weight': '' },
        ]"
        v-text="thirdLine"
      ></p>
      <img v-if="!thirdLineDone" src="../../assets/spinner.gif" alt="" />
      <img v-else src="../../assets/checked.png" width="15" alt="" />
    </div>
  </div>
</template>

<script>
export default {
  name: "LoadingComponent",
  data() {
    return {
      firstLine: "جاري معالجتك اجابتك",
      // firstLineSpinner:true,
      firstLineDone: false,
      //
      secondtLine: "جاري البحث عن الجوائز",
      secondLineDone: false,
      //
      thirdLine: "جاري فحص المخزون",
      thirdLineDone: false,
    };
  },
  methods: {
    start() {
      var vm = this;
      setTimeout(function () {
        vm.firstLine = "لقد قمت بالرد علي 5/5 اسئلة";
        vm.firstLineDone = true;
      }, 2000);
      setTimeout(function () {
        vm.secondtLine = "!لا تزال هناك جوائز متاحه";
        vm.secondLineDone = true;
      }, 4000);
      setTimeout(function () {
        vm.thirdLine = "!تبقي عدد (125) جائزه فقط";
        vm.thirdLineDone = true;
      }, 6000);
      setTimeout(function () {
        vm.$emit("activePrize");
      }, 8000);
    },
  },

  mounted() {
    this.start();
  },
};
</script>

