<template>
  <div class="">
    <Transition name="fade" mode="out-in">
      <component @clicked="onClickServey" :is="step"></component>
    </Transition>
    <div class="flex justify-center gap-4">
      <div
        :style="[
          this.stepColor == 1
            ? { 'background-color': 'white' }
            : { 'background-color': '#E4002B' },
        ]"
        class="rounded-full w-10 h-10"
      ></div>
      <div
        :style="[
          this.stepColor == 2
            ? { 'background-color': 'white' }
            : { 'background-color': '#E4002B' },
        ]"
        class="rounded-full w-10 h-10"
      ></div>
      <div
        :style="[
          this.stepColor == 3
            ? { 'background-color': 'white' }
            : { 'background-color': '#E4002B' },
        ]"
        class="rounded-full w-10 h-10"
      ></div>
      <div
        :style="[
          this.stepColor == 4
            ? { 'background-color': 'white' }
            : { 'background-color': '#E4002B' },
        ]"
        class="rounded-full w-10 h-10"
      ></div>
      <div
        :style="[
          this.stepColor == 5
            ? { 'background-color': 'white' }
            : { 'background-color': '#E4002B' },
        ]"
        class="rounded-full w-10 h-10"
      ></div>
    </div>
  </div>
</template>

<script>
import FirstServeyComponent from "./FirstServeyComponent.vue";
import SecondServeryComponent from "./SecondServeryComponent.vue";
import ThirdServeryComponent from "./ThirdServeryComponent.vue";
import FourthServeryComponent from "./FourthServeryComponent.vue";
import FifthServeryComponent from "./FifthServeryComponent.vue";
export default {
  name: "MainServeyComponent",
  components: {
    FirstServeyComponent,
    SecondServeryComponent,
    ThirdServeryComponent,
    FourthServeryComponent,
    FifthServeryComponent,
  },
  data() {
    return {
      step: "FirstServeyComponent",
      stepColor: 1,
      styleA: "background-color: red  ;",
      styleB: "background-color: red  ;",
    };
  },
  methods: {
    onClickServey(event) {
      if (event == "loadingStep") {
        this.$emit("activeLoading");
      } else {
        this.step = event;
        this.stepColor++;
      }
    },
  },
};
</script>

<style>
.fade-leave-active,
.fade-enter-active {
  transition: opacity 0.5s;
}

.fade-leave-to,
.fade-enter-from {
  opacity: 0;
}
</style>
