import { createStore } from "vuex";

export default createStore({
  state: {
    firstModalShow: false,
    secondModalShow: false,
    thirdModalShow: true,
    fourthModalShow: false,
  },
  getters: {},
  mutations: {
    firstModalOn(state) {
      state.firstModalShow = true;
    },
    firstModalOff(state) {
      state.firstModalShow = false;
    },
    secondModalOn(state) {
      state.secondModalShow = true;
    },
    secondModalOff(state) {
      state.secondModalShow = false;
    },
    thirdModalOn(state) {
      state.thirdModalShow = true;
    },
    thirdModalOff(state) {
      state.thirdModalShow = false;
    },
    fourthModalOn(state) {
      state.fourthModalShow = true;
    },
    fourthModalOff(state) {
      state.fourthModalShow = false;
    },
  },
});
